  background-color: #1a4eec;
  color: #fff;
}
.bg-int-complement:hover,
.bg-int-c:hover {
  background-color: #4771ef;
}
.bg-int-complement:active,
.bg-int-c:active {
  background-color: #103cc2;
}
.bg-int-analog-left,
.bg-int-a1 {
  background-color: #ec4e1a;
  color: #fff;
}
.bg-int-analog-left:hover,
.bg-int-a1:hover {
  background-color: #ef7147;
}
.bg-int-analog-left:active,
.bg-int-a1:active {
  background-color: #c23c10;
}
.bg-int-analog-right,
.bg-int-a2 {
  background-color: #b8ec1a;
  color: #000;
}
.bg-int-analog-right:hover,
.bg-int-a2:hover {
  background-color: #c6ef47;
}
.bg-int-analog-right:active,
.bg-int-a2:active {
  background-color: #96c210;
}
.bg-int-triadic-left,
.bg-int-t1 {
  background-color: #1ab8ec;
  color: #000;
}
.bg-int-triadic-left:hover,
.bg-int-t1:hover {
  background-color: #47c6ef;
}
.bg-int-triadic-left:active,
.bg-int-t1:active {
  background-color: #1096c2;
}
.bg-int-triadic-right,
.bg-int-t2 {
  background-color: #4e1aec;
  color: #fff;
}
.bg-int-triadic-right:hover,
.bg-int-t2:hover {
  background-color: #7147ef;
}
.bg-int-triadic-right:active,
.bg-int-t2:active {
  background-color: #3c10c2;
}
.border-top-radius {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.border-right-radius {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.border-bottom-radius {
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}
.border-left-radius {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.lift-1 {
